import { Router } from '@angular/router';
import { Injectable, OnInit, OnDestroy } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { map, catchError, tap } from 'rxjs/operators';
import { throwError, Subscription } from 'rxjs';
import { MessageService } from '../chat/message.service';

@Injectable()
export class AuthService implements OnInit, OnDestroy {
    private hasLoggedIn = false;
    private apiUrl = 'https://simple-chat-app-bookboost.herokuapp.com/api';
    private usersUrl = 'https://simple-chat-app-bookboost.herokuapp.com/api/users';
    private authUrl = 'https://simple-chat-app-bookboost.herokuapp.com/api/authenticate';
    private token = '';
    private activeUserId: number;
    private signupSubscribtion: Subscription;
    constructor(private router: Router,
        private http: Http) { }

    ngOnInit() {
    }

    ngOnDestroy () {
        this.signupSubscribtion.unsubscribe();
    }

    signupUser(email: string, password: string, name: string) {
        const signupBody = { email: email, password: password, name: name };
        this.signupSubscribtion = this.sendCredentials(signupBody).subscribe(
            user => {
                this.token = user['token'];
                this.activeUserId = user['id'];
                this.hasLoggedIn = true;
                this.router.navigate(['/chat']);
            },
            (error) => {
                console.log(error);
            }
        );
    }

    getActiveToken() { return this.token; }
    getActiveUserId() { return this.activeUserId; }

    getApiUrl() { return this.apiUrl; }


    sendCredentials(body: { email: string, password: string, name: string }) {
        const headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
        const params = `email=${body.email}&password=${body.password}&name=${body.name}`;
        return this.http.post(this.usersUrl, params, { headers: headers }).pipe(map(
            (response: Response) => {
                const data = response.json();
                return data;
            }));
    }

    getToken(body: { email: string, password: string }) {
        const headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
        const params = `email=${body.email}&password=${body.password}`;
        return this.http.post(this.authUrl, params, { headers: headers }).pipe(map(
            (response: Response) => {
                const data = response.json();
                return data;
            }));
    }

    signinUser(email: string, password: string) {
        const signinBody = { email: email, password: password };
        this.getToken(signinBody).subscribe((user) => {
            this.token = user['token'];
            this.activeUserId = user['id'];
            console.log('comming straight from the api: ', this.activeUserId);
            this.hasLoggedIn = true;
            this.router.navigate(['/chat']);
        });
    }

    logout() {
        console.log('User logged out!');
        this.token = '';
        this.activeUserId = -1;
        this.hasLoggedIn = false;
        this.router.navigate(['/signin']);
    }

    isAuthenticated() {
        return this.hasLoggedIn;
    }
}
